import React, { useState, useRef } from 'react';
import { Button, Row, Col, Space, Tooltip } from 'antd';
import { ReloadOutlined, SearchOutlined } from '@ant-design/icons';

import CreateChannels from './create_channels';
import ChannelsQueryDrawerForm from './channels_query_form';
import ChannelsList from './channels_list';
import ChannelsQueryParams from './channels_query_params_display';
// Channels 入口组件

const ChannelsComponent = () => {
  const reqParams = useRef({ page: 0 });

  const [reloadListCount, setReloadListCount] = useState(1);

  const [drawerVisible, setDrawerVisible] = useState(false);

  const [createChannelsVisible, setCreateChannelsVisible] = useState(false);

  const queryHandle = (currentRequestParams) => {
    // 查询操作
    reqParams.current = { ...currentRequestParams };
    // 触发重新读取数据
    setReloadListCount(reloadListCount + 1)
    console.log('search...');
  };

  const initReloadList = () => {
    // 刷新列表, 删除所有查询条件
    reqParams.current = { page: 0 };
    setReloadListCount(reloadListCount + 1);
  }

  return (
    <div>
      <Row>
        <Col span={14}>
          <Space>
            <h4>Channels</h4>
            <Tooltip title="刷新">
              <Button onClick={initReloadList} shape="circle" icon={<ReloadOutlined />}></Button>
            </Tooltip>
            <Tooltip title="搜索">
              <Button onClick={() => setDrawerVisible(true)} shape="circle" icon={<SearchOutlined />}></Button>
            </Tooltip>
          </Space>
        </Col>
        <Col span={10}>
          <div style={{ width: '100%', height: 'auto' }}>
            <Button style={{ float: 'right' }} type="link" onClick={() => setCreateChannelsVisible(true)}>新建</Button>
          </div>
        </Col>
      </Row>
      <Row>
        <Col span={24}><ChannelsQueryParams paramsObj={reqParams.current}></ChannelsQueryParams></Col>
      </Row>
      <ChannelsQueryDrawerForm
        visible={drawerVisible}
        setVisible={setDrawerVisible}
        queryHandle={queryHandle}
      ></ChannelsQueryDrawerForm>

      <ChannelsList
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
        requestParams={reqParams.current}
      ></ChannelsList>

      <CreateChannels
        visible={createChannelsVisible}
        setVisible={setCreateChannelsVisible}
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
      ></CreateChannels>
    </div>
  )
};

export default ChannelsComponent;
